import React, { Component, PropTypes } from 'react';

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import FloatingActionButton from 'material-ui/FloatingActionButton';
import ContentAdd from 'material-ui/svg-icons/content/add';

import Toggle from 'material-ui/Toggle';

import Checkbox from 'material-ui/Checkbox';

import { RadioButton, RadioButtonGroup } from 'material-ui/RadioButton';

import TextField from 'material-ui/TextField';

import FlatButton from '../mui-components/FlatButton';
import RaisedButton from '../mui-components/RaisedButton';

import getMuiTheme from '../lib/getMuiTheme';
import createCustomMuiTheme from '../lib/createCustomMuiTheme';

import './ComponentView.css';

const propTypes = {
  palette: PropTypes.object.isRequired,
};

const style = { margin: '12px' };


class ComponentView extends Component {

  render() {
    const theme = createCustomMuiTheme(this.props.palette);

    return (
      <MuiThemeProvider muiTheme={getMuiTheme(theme)}>
        <div className="component-view">
          <div>
            <FlatButton style={style} label="Normal" />
            <FlatButton style={style} label="Disabled" disabled />
          </div>

          <div>
            <FlatButton style={style} label="Normal" primary />
            <FlatButton style={style} label="Disabled" primary disabled />
          </div>

          <div>
            <RaisedButton style={style} label="Normal" />
            <RaisedButton style={style} label="Disabled" disabled />
          </div>

          <div>
            <RaisedButton style={style} label="Normal" primary />
            <RaisedButton style={style} label="Disabled" primary disabled />
          </div>

          <div>
            <FloatingActionButton style={style}>
              <ContentAdd />
            </FloatingActionButton>
            <FloatingActionButton style={style} disabled>
              <ContentAdd />
            </FloatingActionButton>
          </div>

          <div>
            <Toggle style={style} />
            <Toggle defaultToggled style={style} />
          </div>

          <div>
            <Checkbox style={style} />
            <Checkbox defaultChecked style={style} />
          </div>

          <div>
            <RadioButtonGroup name="foobarRadio" defaultSelected="foo">
              <RadioButton
                value="foo"
                style={style}
              />
              <RadioButton
                value="bar"
                style={style}
              />
            </RadioButtonGroup>
          </div>

          <div>
            <TextField
              hintText="Hint text"
              style={style}
            />
          </div>

          <div>
            <TextField
              defaultValue="Input text for a single line text field"
              floatingLabelText="Single-line input label"
              style={style}
            />
          </div>
        </div>
      </MuiThemeProvider>
    );
  }
}

ComponentView.propTypes = propTypes;

export default ComponentView;



